<template>
  <div class="container-fluid p-0" v-if="authenticated">
    <div class="row">
      <div class="col-md-12 mb-3">
        <h1 class="h1 mb-0">Kerja Praktek</h1>
      </div>
      <div class="col-md-12">
        <div class="card">
          <div class="card-body">
            <div v-if="getStatusKP != null || getStatusKP != 0">
              <DataKP />
            </div>
            <div v-if="getStatusKP === null"><IsiDataKP /></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import DataKP from "../../components/pendaftaran/kerjapraktek/DataKP.vue";
import IsiDataKP from "../../components/pendaftaran/kerjapraktek/IsiDataKP.vue";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "KerjaPraktek",
  data() {
    return {};
  },
  components: {
    DataKP,
    IsiDataKP,
  },
  computed: {
    ...mapGetters({
      authenticated: "auth/authenticated",
      getStatusKP: "statusDaftar/getStatus",
    }),
  },
  methods: {
    ...mapActions({
      statusDaftar: "statusDaftar/getReport",
    }),
    getStatus() {
      this.statusDaftar();
    },
  },
 
  mounted() {
    $("title").html("Kerja Praktek | STTBANDUNG");
    this.getStatus();
  },
};
</script>

