<script>
import EditKP from "@/components/pendaftaran/kerjapraktek/EditKP";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "DataKP",
  components: {
    EditKP,
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      getDatakp: "kp/getKP",
    }),
  },
  methods: {
    ...mapActions({
      datakpall: "kp/kp",
    }),
    getAllKP() {
      this.datakpall();
    },
  },
  mounted() {
    this.getAllKP();
  },
};
</script>

<style>
.check {
  color: green;
}
.times {
  color: red;
}
</style>