<template>
  <form @submit.prevent="submit">
    <div class="modal-body">
      <div class="form-group">
        <label for="inputJudul">Judul Kerja Praktek</label>
        <input
          id="inputJudul"
          type="text"
          class="form-control"
          v-model="formEdit.title"
          placeholder="Kosongkan jika tidak ingin diubah"
        />
      </div>
      <div class="form-group">
        <label for="nmPerusahaan">Nama Perusahaan</label>
        <input
          id="nmPerusahaan"
          type="text"
          class="form-control"
          v-model="formEdit.instance"
          placeholder="Kosongkan jika tidak ingin diubah"
        />
      </div>
      <div class="form-group">
        <label for="inputPembimbing">Pembimbing Dari Perusahaan</label>
        <input
          id="inputPembimbing"
          type="text"
          class="form-control"
          v-model="formEdit.guide"
          placeholder="Kosongkan jika tidak ingin diubah"
        />
      </div>

      <!-- <div class="form-group">
      <label for="inputAddress"
        >File Laporan <small> (File harus pdf max file 10MB)</small></label
      >
      <input type="file" class="form-control h-25" id="customFile" />
    </div>
    <div class="form-group">
      <label for="inputAddress">
        File Absensi
        <small> (File harus pdf max file 10MB)</small></label
      >
      <input type="file" class="form-control h-25" id="customFile" />
    </div>
    <div class="form-group">
      <label for="inputAddress" class="w-100"
        >File Nilai KP <small> (File harus pdf max file 10MB)</small></label
      >
      <input type="file" class="form-control h-25" id="customFile" />
    </div> -->
    </div>

    <div class="modal-footer">
      <button
        type="button"
        class="btn btn-outline-secondary"
        data-dismiss="modal"
      >
        Batal
      </button>
      <button type="submit" class="btn btn btn-primary">Kirim</button>
    </div>
  </form>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "EditKP",
  inject: ["notyf"],
  data() {
    return {
      formEdit: {
        title: "",
        instance: "",
        guide: "",
      },
    };
  },
  computed: {
    ...mapGetters({
      getDatakp: "kp/getKP",
    }),
  },
  methods: {
    ...mapActions({
      datakpall: "kp/kp",
    }),
    getAllKP() {
      this.datakpall();
    },
    async submit() {
      try {
        let dataEdit = {
          practice_id: this.getDatakp.practice_id,
          instance_name: this.formEdit.instance,
          title_ina: this.formEdit.title,
          guide_external_name: this.formEdit.guide,
        };

        let response = await axios.put("/siakad/edit-practice", dataEdit);
        this.getAllKP();
        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        console.warn(error);
      }
    },
  },
  mounted() {
    this.getAllKP();
    this.formEdit.title = this.getDatakp.title_ina;
    this.formEdit.instance = this.getDatakp.instance_name;
    this.formEdit.guide = this.getDatakp.external_guide_name;
    // console.log(this.formEdit.guide);
  },
};
</script>

