<template>
  <div class="col-12 col-xl-8">
    <p>
      * Jika info dengan tanda ini salah, maka bisa diperbaiki melalui
      pemutakhiran data
    </p>
    <form @submit.prevent="submit">
      <div class="form-group text-dark">
        <label for="inputHp">Nomer Hp *</label>
        <input
          type="text"
          class="form-control"
          id="inputHp"
          :placeholder="getDataHP.handphone_number"
          disabled
        />
      </div>
      <div class="form-group text-dark">
        <label for="inputJudulkp">Judul Kerja Praktek</label>
        <textarea
          type="text"
          class="form-control"
          id="inputJudulkp"
          v-model="form.title_ina"
          required
        />
        <p><small> e.g "Sistem Informasi Di PT.XXX"</small></p>
      </div>
      <div class="form-group text-dark">
        <label for="nmPerusahaan">Nama Perusahaan</label>
        <input
          type="text"
          class="form-control"
          id="nmPerusahaan"
          v-model="form.instance_name"
          required
        />
        <p><small> e.g "PT. Cirebon Power Services"</small></p>
      </div>
      <div class="form-group text-dark">
        <label for="inputPembimbing">Pembimbing dari Perusahaan</label>
        <input
          type="text"
          class="form-control"
          id="inputPembimbing"
          v-model="form.guide_external_name"
          required
        />
      </div>
      <div class="form-group text-dark">
        <label for="dosenPembimbing">Dosen Pembimbing dari Kampus</label>
        <input
          type="text"
          class="form-control"
          id="dosenPembimbing"
          placeholder="Dosen Pembimbing dari Kampus"
          :value="getDataDospem.full_name"
          disabled
        />
      </div>
      <div class="form-group text-dark">
        <label for="fileLaporan"
          >File Laporan Kerja Praktek<small>
            (File harus pdf max file 10MB)</small
          ></label
        >
        <input type="file" class="form-control h-25" id="fileLaporan" />
      </div>
      <div class="form-group text-dark">
        <label for="fileAbsensi"
          >File Absensi Kerja Praktek<small>
            (File harus pdf max file 10MB)</small
          ></label
        >
        <input type="file" class="form-control h-25" id="fileAbsensi" />
      </div>
      <div class="form-group text-dark">
        <label for="fileNIlaiKP"
          >File Nilai KP dari Perusahaan<small>
            (File harus pdf max file 10MB)</small
          ></label
        >
        <input type="file" class="form-control h-25" id="fileNIlaiKP" />
      </div>
      <div class="form-group text-dark">
        <h5>Tanda Tangan Mahasiswa</h5>
        <p>
          <small
            >Klik gambar untuk menginput tanda tangan (jika tanda tangan belum
            ada)</small
          >
        </p>
        <div class="row">
          <p class="col-5 col-md-4">
            <textarea class="form-control rounded-0" rows="5"> </textarea>
          </p>
          <div>
            <button class="btn btn-primary btn-success btn-sm mt-5">
              <i class="fas fa-sync-alt"></i>
            </button>
          </div>
        </div>
      </div>
      <button type="submit" class="btn btn-primary btn-success">
        Pengajuan Sekarang
      </button>
      <br />
    </form>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "IsiDataKP",
  inject: ["notyf"],
  data() {
    return {
      form: {
        npm: null,
        title_ina: null,
        instance_name: null,
        guide_external_name: null,
      },
      // handphone_number: {
      //   handphone_number: "",
      // },
    };
  },
  computed: {
    ...mapGetters({
      getDataHP: "profile/profile",
      getDataDospem: "dospem/datadospem",
      getNPM: "auth/users",
    }),
  },
  methods: {
    ...mapActions({
      profile: "profile/profile",
      dospem: "dospem/dospem",
    }),
    getProfile() {
      this.profile();
    },
    getDospem() {
      this.dospem();
    },
    getNPMmhs() {
      this.form.npm = this.getNPM.username;
    },

    async submit() {
      try {
        let dataKP = {
          npm: this.form.npm,
          title_ina: this.form.title_ina,
          instance_name: this.form.instance_name,
          guide_external_name: this.form.guide_external_name,
        };
        let response = await axios.post("/siakad/insert-practice", dataKP);

        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        return response;
      } catch (error) {
        // console.warn(error);
        console.warn(error);
      }
    },
  },

  mounted() {
    this.getProfile();
    this.getDospem();
    this.getNPMmhs();
  },
};
</script>

